
.add-todo__input {
    border: 1px solid #ddd;
    margin-bottom: 15px;
}
.add-todo__button {
    background: #2196F3;
}
.cancel__button {
    background: #F44336;
}
.add-todo__wrapper {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    cursor: auto;
    background: transparent;
    -webkit-animation-name: fadeIn;
            animation-name: fadeIn;
    -webkit-animation-duration: 0.25s;
            animation-duration: 0.25s;
}
.add-todo__wrapper .buttons {
    float: right;
}
.add-todo__wrapper .button {
    padding: 12px 30px;
    font-size: 14px;
    font-weight: bold;
    color: #fff;
    float: left;
    margin-left: 15px;
}
.add-todo__wrapper .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: rgba(0, 0, 0, 0.3);
}
.add-todo__wrapper .add-todo {
    z-index: 2;
    background: #fff;
    position: absolute;
    top: 30%;
    left: 50%;
    -webkit-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    transform: translateX(-50%);
    opacity: 0;
    -webkit-animation-name: slideIn;
    animation-name: slideIn;
    -webkit-animation-duration: 0.25s;
    animation-duration: 0.25s;
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
    -webkit-animation-delay: 0.25s;
    animation-delay: 0.25s;
    padding: 30px;
    min-width: 300px;
}



