
.todo-item {
    font-size: 18px;
    color: #333;
    width: 100%;
    background: #fff;
    margin-bottom: 15px;
    border: 1px solid #ddd;
    border-radius: 6px;
    cursor: pointer;
}
.todo-item .text {
    display: block;
    width: 100%;
    padding: 15px;
}
.todo-item .buttons {
    display: block;
    width: 100%;
    text-align: right;
    padding: 15px;
    border-top: 1px solid #ddd;
}
.todo-item button {
    display: inline-block;
    font-size: 14px;
    outline: 0;
    margin-left: 15px;
}
.todo-item button svg {
    width: 20px;
    height: auto;
    fill: #333;
    transition: 0.25s;
}
.todo-item .remove-button:hover svg {
    fill: tomato;
}
.todo-item__focus {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    cursor: auto;
    background: transparent;
    -webkit-animation-name: fadeIn;
            animation-name: fadeIn;
    -webkit-animation-duration: 0.25s;
            animation-duration: 0.25s;
}
.todo-item__focus .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: rgba(0, 0, 0, 0.3);
}
.todo-item__focus-inner {
    width: 280px;
    z-index: 2;
    background: #fff;
    position: absolute;
    top: 30%;
    left: 50%;
    -webkit-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    transform: translateX(-50%);
    opacity: 0;
    -webkit-animation-name: slideIn;
    animation-name: slideIn;
    -webkit-animation-duration: 0.25s;
    animation-duration: 0.25s;
    -webkit-animation-fill-mode: forwards;
    animation-fill-mode: forwards;
    -webkit-animation-delay: 0.25s;
    animation-delay: 0.25s;
}
@-webkit-keyframes fadeIn {
from {
        opacity: 0;
}
to {
        opacity: 1;
}
}
@keyframes fadeIn {
from {
        opacity: 0;
}
to {
        opacity: 1;
}
}
@-webkit-keyframes slideIn {
from {
        opacity: 0;
        -webkit-transform: translateX(-50%) translateY(60px);
                transform: translateX(-50%) translateY(60px);
}
to {
        opacity: 1;
        -webkit-transform: translateX(-50%) translateY(0);
                transform: translateX(-50%) translateY(0);
}
}
@keyframes slideIn {
from {
        opacity: 0;
        -webkit-transform: translateX(-50%) translateY(60px);
                transform: translateX(-50%) translateY(60px);
}
to {
        opacity: 1;
        -webkit-transform: translateX(-50%) translateY(0);
                transform: translateX(-50%) translateY(0);
}
}
.completed {
    text-decoration: line-through;
}
.completed .complete-button svg {
    fill: #4CAF50;
}
.completed input {
    text-decoration: line-through;
}



