
.lists-wrapper {
    width: 1100px;
    margin: 0 auto;
    padding: 0 15px;
    max-width: 100%;
}
.lists {
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
    float: left;
    width: 100%;
    padding: 30px 0;
}
.list {
    width: calc(20% - 16px);
    margin-right: 20px;
    margin-bottom: 20px;
    padding: 15px;
    border: 1px solid #ddd;
    min-height: 100px;
    cursor: pointer;
}
.list:nth-child(5n+5) {
    margin-right: 0;
}
@media screen and (max-width: 920px) {
    .list:nth-child(5n+5) {
        margin-right: 20px;
    }
}
@media screen and (max-width: 920px) {
    .list {
        width: calc(25% - 16px);
    }
    .list:nth-child(4n+4) {
        margin-right: 0;
    }
}
@media screen and (max-width: 700px) {
    .list {
        width: calc(33.33333% - 16px);
    }
    .list:nth-child(4n+4) {
        margin-right: 20px;
    }
    .list:nth-child(3n+3) {
        margin-right: 0;
    }
}
@media screen and (max-width: 700px) {
    .list {
        width: calc(50% - 10px);
    }
    .list:nth-child(3n+3) {
        margin-right: 20px;
    }
    .list:nth-child(2n+2) {
        margin-right: 0;
    }
}
.list h2 {
    margin: 0;
    font-size: 20px;
    font-weight: 400;
    margin-bottom: 30px;
    word-break: break-all;
}
@media screen and (max-width: 920px) {
    .list h2 {
        font-size: 18px;
    }
}
.add-list {
    border: 1px solid #ddd;
    width: 260px;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-wrap: wrap;
        -ms-flex-wrap: wrap;
            flex-wrap: wrap;
    padding: 0;
}
.add-list input {
    margin-bottom: 15px;
    border-bottom: 1px solid #ddd;
    font-size: 16px;
}
.add-list button {
    padding: 10px 18px;
    font-size: 14px;
    color: #fff;
    margin-right: 15px;
    background: #2196F3;
    margin-bottom: 15px;
    margin-left: auto;
}



